:root {
  --maincolor: #ffcdb2;
  --basecolor: #6d6875;
  --accentcolor1: #ffb4a2;
  --accentcolor2: #e5989b;
  --accentcolor3: #b5838d;
  --whitecolor: #ffffff;
}
.app {
  font-family: Helvetica, sans-serif;
  background-color: var(--maincolor);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--basecolor);
}
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 50%;
}

.contact-form > input {
  height: 2.5rem;
  border: none;
  border-radius: 2px;
  background-color: var(--accentcolor1);
  font-size: 1.4rem;
  color: var(--basecolor);
  padding: 0.5rem;
}

.contact-form > textarea {
  height: 7rem;
  border: none;
  border-radius: 2px;
  background-color: var(--accentcolor2);
  font-size: 1.4rem;
  color: var(--basecolor);
  padding: 0.5rem;
  resize: none;
}

input::placeholder,
textarea::placeholder {
  color: var(--basecolor);
}

.contact-form > button {
  height: 3rem;
  border: none;
  border-radius: 2px;
  background-color: var(--accentcolor3);
  font-size: 1.4rem;
  color: var(--whitecolor);
  padding: 0.5rem;
  cursor: pointer;
  transition: 0.4s ease-in-out;
}

.contact-form > button:hover {
  transition: 0.4s ease-in-out;
  background-color: var(--basecolor);
}